
//获取数据
export function getData(root, url, params){
    root.service.get(url, {params: params || {}}).then(res => {
        if(res.data.status === 200){
            root.tableData = res.data.data
            root.total = res.data.total
        }else {
            root.$message.error("获取数据失败:"+res.data.message);
        }
    }).catch(err =>{
        throw err;
    })
}

//新增 and 修改
import qs from 'qs'
export function changeInfo(root, url, method, form){
    let data = qs.stringify(form);
    root.service[method](url, data).then(res => {
        if(res.data.status === 200){
            root.dialogFormVisible = false; 
            root.$message.success(res.data.message);
            getData(root, url)
        }else {
            root.$message.error(res.data.message);
        }
    }).catch(err =>{
        throw err;
    })
}

//删除
export function delData(root, url, id){
    root.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
    }).then(() => {
        root.service.delete(`${url}/${id}`).then(res => {
            if(res.data.status === 200){
                root.$message.success(res.data.message);
                getData(root, url) //重新查询
            }else {
                root.$message.error(res.data.message);
            }
      }).catch(err => {
        root.$message.error(err);
      })  
    }).catch(() => {
        // root.$message('取消删除');
    });
}

//获取表格数据
export function getTableData(root, url, params){
    root.service.get(url, {params: params || {}}).then(res => {
        if(res.data.status === 200){
            root.tableData = res.data.data
            root.total = res.data.total
            root.loading = false
        }else {
            root.loading = false
            root.$message.error(res.data.message);
        }
    }).catch(err =>{
        root.loading = false
        throw err;
    })
    
}