<template>
  <div class="infoMent">
    <el-form :inline="true"  class="demo-form-inline" size="small">
      <el-form-item>
        <el-button type="primary" @click="addStudent">新增</el-button>
      </el-form-item>
    </el-form>

    <el-table :data="tableData" border style="width: 100%">
      <el-table-column prop="id" label="序号"> </el-table-column>
      <el-table-column prop="name" label="姓名" width="180"> </el-table-column>
      <el-table-column prop="sex" label="性别"> </el-table-column>
      <el-table-column prop="age" label="年龄"> </el-table-column>
      <el-table-column prop="father" label="父亲姓名"> </el-table-column>
      <el-table-column prop="mather" label="母亲姓名"> </el-table-column>
      <el-table-column prop="time" label="入校时间"> </el-table-column>
      <el-table-column prop="address" label="家庭住址"> </el-table-column>
      <el-table-column prop="phone" label="	联系方式"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" circle @click="edit(scope.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" circle @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page=currentPage
      :page-sizes="[10, 20, 50, 100]"
      :page-size=pageSize
      layout="total, sizes, prev, pager, next, jumper"
      :total= total>
    </el-pagination>

    <!-- 新增Dialog -->
    <el-dialog :title="state ? '新增学生信息' : '修改学生信息'" :visible.sync="dialogFormVisible" width="500px" >
      <el-form :model="form"  :rules="rules" ref="form">
        <el-form-item label="姓名" prop="name" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex"  :label-width="formLabelWidth">          
          <el-radio v-model="form.sex" label="1">男</el-radio>
          <el-radio v-model="form.sex" label="2">女</el-radio>
        </el-form-item>
        <el-form-item label="年龄" prop="age" :label-width="formLabelWidth">
          <el-input v-model="form.age"></el-input>
        </el-form-item>
        <el-form-item label="父亲姓名" prop="father" :label-width="formLabelWidth">
          <el-input v-model="form.father"></el-input>
        </el-form-item>
        <el-form-item label="母亲姓名" prop="mather" :label-width="formLabelWidth">
          <el-input v-model="form.mather"></el-input>
        </el-form-item>
        <el-form-item label="入校时间" prop="time" :label-width="formLabelWidth">
          <!-- <el-input v-model="form.time"></el-input> -->
          <el-date-picker v-model="form.time" type="date" format="yyyy/MM/dd" value-format="yyyy-MM-dd" placeholder="选择日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="家庭住址" prop="address" :label-width="formLabelWidth">
          <el-input v-model="form.address"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" prop="phone" :label-width="formLabelWidth">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sure('form')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {info,  getInfo, infoDel} from '@/api/api'
import {getData, changeInfo, delData} from '@/utils/table'

export default {
  data() {
    return {
      total: 0, //数据总条数
      currentPage: 1,   //当前页码
      pageSize: 10, //每页条数
      state: true,  //true新增，false修改
      
      tableData: [],
      dialogFormVisible: false,  //新增窗口显示
      form: {
        name : "",
        sex : "1",
        age : "",
        father : "",
        mather : "",
        time : "",
        address : "",
        phone : "",
      },  //新增窗口数据
      formLabelWidth: '80px',
      rules: {
        name:[{required: true, message: '请输入姓名'}],
        sex:[{required: true}],
        age:[{required: true, message: '请输入年龄'}],
        time:[{required: true, message: '请输入入学时间'}],
        address:[{required: true, message: '请输入地址'}],
        phone:[{required: true, message: '请输入联系方式'}],
      },
    }
  },

  created() {
    // this.getData();
    getData(this, '/info')
  },

  methods: {
    getData() {
      getData(this, '/info')
      // getInfo().then(res =>{
      //   if(res.data.status === 200){
      //     this.tableData = res.data.data;
      //     this.total = res.data.total;
      //   }else {
      //     this.$message.error("获取数据失败:"+res.data.message);
      //   }
      // }).catch(err =>{
      //   this.$message.error(err);
      //   throw err;
      // })
    },

    // 删除
    del(row){
      delData(this, '/info', row.id)
      // this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
      //   confirmButtonText: '确定',
      //   cancelButtonText: '取消',
      //   type: 'warning'
      // }).then(() => {
      //   infoDel(row.id).then(res => {
      //     if(res.data.status === 200){
      //       this.$message({ type: 'success', message: '删除成功!' });
      //       this.getData(); //重新查询
      //     }else {
      //       this.$message.error('删除失败：'+res.data.message);
      //     }
      //   }).catch(err => {
      //     this.$message.error('删除失败：'+err);
      //   })  
      // }).catch(() => {
      //   this.$message('取消删除');
      // });
    },

    // 修改
    edit(row){
      this.state = false;
      this.form = {...row};
      this.dialogFormVisible = true;
    },
    // 新增
    addStudent() {  
      this.state = true;
      this.form = {
        name : "",
        sex : "1",
        age : "",
        father : "",
        mather : "",
        time : "",
        address : "",
        phone : "",
      }
      this.dialogFormVisible = true;
    },

    //新增确定
    sure(form) {
      this.$refs[form].validate((valid) =>{
        if(valid){ 
          // if(this.state){ //新增         
          //   changeInfo(this, '/info', 'post', this.form)            
          // }else { //修改
          //   changeInfo(this, '/info', 'put', this.form)
          // }
          let method = this.state ? 'post' : 'put'
          changeInfo(this, '/info', method, this.form)
          
        }
      });
    },

    closeInfo(){
      console.log("123")
      this.$refs['form'].resetFields();
      // this.$refs.form.resetFields();
    },

    //分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      this.currentPage = 1;
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
    },
  },

  watch: {
    dialogFormVisible: function(newVal, oldVal){
      if(!newVal){
        this.closeInfo()
      }
    }
  }

}
</script>

<style lang="scss" scoped>
  .infoMent {
    .demo-form-inline, .el-form-item {
      text-align: left;
    }
    .el-pagination {
      text-align: left;
      margin-top: 20px;
    }

  }
</style>