import service from "@/service";
import qs, { stringify } from 'qs'

// 登录接口
export function login(data){
    return service({
        method: 'post',
        url: '/login',
        data: data
    })
}

// 学生列表查询接口
export function students(params){
    return service({
        method: 'get',
        url: '/students',
        params
    })
}

// 学生列表删除接口
export function studentDel(id){
    return service({
        method: 'delete',
        url: `/students/${id}`
    })
}

