//信息列表 新增and修改接口
export function info(type, data) {
    data = stringify(data);
    let obj = {method: type, url: '/info', data}
    return service(obj)
}


//信息列表 查询接口
export function getInfo() {
    return service({
        method: 'get',
        url: '/info'
    })
}

// 信息列表删除接口
export function infoDel(id){
    return service({
        method: 'delete',
        url: `/info/${id}`
    })
}

// 旅游地图接口
export function travel() {
    return service({
        method: 'get',
        url: '/travel'
    })
}